import React from 'react';
import { Row, Col, Form, Button } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

function Step0() {
  const navigate = useNavigate();

  const handleSubmit = (event) => {
    navigate('/v2/step1');
  };

  return (
    <Form
      name="signup"
      id="signup"
      className="needs-validation d-flex flex-column justify-content-center"
      noValidate
      onSubmit={handleSubmit}
      style={{ minHeight: '100vh' }}
    >
      <Row className="align-items-center mb-4">
        <Col md={6}>
          <p className="h3 mb-0">  1:1  </p>
          <h2 className="mb-0 fw-bold">KB </h2>
          <br></br>
          <picture>
            <img
              src={process.env.PUBLIC_URL + '/img.png'}
              alt="Alternative text for the image"
              className="img-fluid rounded"
            />
          </picture>
        </Col>
      </Row>
      <Row className="justify-content-start">
        <Col md={6}>
          <ul className="text-muted">
            <li>     .</li>
            <li>     . </li>
            <li>     . </li>
          </ul>
        </Col>
      </Row>
      <Row className="justify-content-end">
        <Col md={6} xs={12} className="mb-3 d-flex justify-content-end">
          <Button variant="warning" size="sm" type="submit">
            
          </Button>
        </Col>
      </Row>
    </Form>
  );
}

export default Step0;
